import React from "react";
import { useDispatch } from "react-redux";
import { clearItemFromCart, addItem, removeItem } from "store/actions/cart/cart.actions";

const CheckoutItem = ({cartItem}) => {
  const dispatch = useDispatch()

  const { img, price, title, quantity } = cartItem;  
  const subTotal =  quantity * price
  
  return (
    <div className="checkout-item">
      <div className="image-container">
        <img src={img} alt="item" />
      </div>
      <span className="name">{title}</span>
      <span className="quantity">
      <div className='arrow' onClick={()=>dispatch(clearItemFromCart(cartItem))}>&#10094;</div>
      <span className='value'>{quantity}</span>
      <div className='arrow' onClick={()=>dispatch(addItem(cartItem))}>&#10095;</div>
      </span>
      <span className="price">{price}</span>
      <span className="price">{subTotal}</span>
      <div className="remove-button" onClick={()=>dispatch(removeItem(cartItem))}>
        &#10005;
      </div>
    </div>
  );
};


export default CheckoutItem;
